import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';

import { AboutUsComponent } from './about-us/about-us.component';
import { RegistrationComponent } from './registration/registration.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { HomeComponent } from './home/home.component';
import { StartupLoginPageComponent } from './login/startup-login/startup-login-page/startup-login-page.component';
import { InvestorLoginPageComponent } from './login/Investor-login/investor-login-page/investor-login-page.component';


const routes: Routes /*Strongly typed*/ = [
  { path : 'login', component:LoginComponent},
  {path:'registration', component:RegistrationComponent},
  {path:'aboutus',component:AboutUsComponent},
  {path:'contactus',component:ContactUsComponent},
  {path:'home',component:HomeComponent},
  {path:'s-login', component:StartupLoginPageComponent},
  {path:'i-login', component:InvestorLoginPageComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const routingComponents = [LoginComponent,RegistrationComponent,AboutUsComponent]
